#guidelines h2 {
    margin: 0 0 1.25rem;
}

/**
 * Guidelines Header
 */
.guidelines__header {
    background-image: url("//s1.city.ac.uk/cityr/i/guidelines/background.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    color: white;
    width: 100%;
    padding-bottom: 3.75rem;
    overflow: hidden;
}

.guidelines__header .guidelines__header__logo {
    background-color: rgba(255, 255, 255, 0.9);
    display: inline-block;
    padding: 1.25rem 1.25rem 2.5rem;
    border-bottom-width: 0;
}

.guidelines__header .guidelines__header__logo span {
    display: none;
}

.guidelines__header .guidelines__header__title,
.guidelines__header .guidelines__header__description {
    display: block;
    text-align: center;
}

.guidelines__header .guidelines__header__title {
    margin: 6.25rem 0 1.25rem;
    font-size: 4rem;
    line-height: 1;
}

.guidelines__header .guidelines__header__description {
    font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica, sans-serif;
    font-size: 1.5rem;
    font-weight: 400;
    line-height: 1.5;
}

.guidelines__nav-primary {
    background-color: lightgray;
}

.guidelines__nav-primary__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 2.5rem 0 1.25rem !important;
    margin-top: 0;
    margin-bottom: 0;
    list-style: none;
}

.guidelines__nav-primary__list li {
    display: inline-block;
    transition: width 0.15s;
}

@media (max-width: 500px) {
    .guidelines__nav-primary__list li {
        width: 100%;
    }
}

@media (min-width: 501px) and (max-width: 800px) {
    .guidelines__nav-primary__list li {
        width: 50%;
    }
}

@media (min-width: 801px) {
    .guidelines__nav-primary__list li {
        width: 33.333%;
    }
}

.guidelines__nav-primary__list a {
    width: 100%;
    display: block;
    padding: 1em;
    border-bottom-width: 0;
    text-decoration: none;
    transition: all 0.3s ease;
}

.guidelines__nav-primary__list a,
.guidelines__nav-primary__list a .guidelines__nav-primary__list__description {
    color: black;
}

.guidelines__nav-primary__list a .guidelines__nav-primary__list__title {
    display: block;
    margin-top: 0;
    margin-bottom: 0.5em;
    font-size: 1.125em;
    font-weight: 600;
    line-height: 1;
}

.guidelines__nav-primary__list a .guidelines__nav-primary__list__title:after {
    content: '\f105';
    float: right;
    font-size: 1.5em;
    font-family: FontAwesome;
    font-weight: 400;
    line-height: 0.667;
}

.guidelines__nav-primary__list a .gudielines__nav-primary__list__description {
    line-height: 1.5;
}

#guidelines p {
    margin-top: 0;
}

.guidelines__main-content {
    margin-top: 3.75rem;
}

@media (min-width: 801px) {
    .guidelines__main-content:not(.main-content--full) {
        padding-right: 22.5rem;
    }
}

.guidelines__aside {
    width: 20rem;
}

@media (max-width: 800px) {
    .guidelines__aside {
        width: 100%;
        margin-bottom: 1.25rem;
    }
}

@media (min-width: 801px) {
    .guidelines__aside {
        float: right;
        margin-right: -22.5rem;
        border-top: 0.25em solid lightgray;
        padding-top: 2.5rem;
    }
}

.guidelines__aside p:last-child {
    margin-bottom: 0;
}

.guidelines__atomic-icons-list {
