}

.guidelines__case-studies__list .guidelines__case-studies__list__media .icon {
    background-color: red;
    color: white;
    width: 3em;
    height: 3em;
    border-bottom-width: 0;
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
