}

[id^='presentation-view--desktop']:checked ~ .guidelines-component__controls .guidelines-component__controls__presentation--desktop:after {
    opacity: 1;
}

[id^='presentation-view--tablet']:checked ~ .guidelines-component__presentation iframe,
[id^='presentation-view--tablet']:checked ~ .guidelines-component__presentation .guidelines-component__presentation__contents {
