}

[id^='presentation-view--tablet']:checked ~ .guidelines-component__controls .guidelines-component__controls__presentation--tablet {
    background-color: #1a1a1a !important;
}

[id^='presentation-view--tablet']:checked ~ .guidelines-component__controls .guidelines-component__controls__presentation--tablet:after {
    opacity: 1;
}

[id^='presentation-view--mobile']:checked ~ .guidelines-component__presentation iframe,
[id^='presentation-view--mobile']:checked ~ .guidelines-component__presentation .guidelines-component__presentation__contents {
