.guidelines-component {
    background-color: lightgray;
    width: auto;
    max-width: 100%;
    display: inline-block;
    padding: 1.5em 1em;
    margin: 0 auto;
    position: relative;
    text-align: left;
}

.guidelines-component + .guidelines-component {
    margin-top: 1.5em;
}

.guidelines-component__title {
    padding-bottom: 1em;
}

.guidelines-component__presentation,
.guidelines-component__presentation iframe,
.guidelines-component__presentation__contents,
.guidelines-component__controls,
.guidelines-component__code {
    transition: width 0.5s ease;
}

.guidelines-component__presentation {
    background-color: white;
    max-width: 100%;
    border: 2px solid #b21c1d;
    position: relative;
    overflow-x: auto;
    overflow-y: hidden;
}

.guidelines-component__presentation iframe,
.guidelines-component__presentation .guidelines-component__presentation__contents {
    display: block;
    border: 0;
}

.guidelines-component__controls,
.guidelines-component__code {
    max-width: 768px;
    display: block;
    margin: 0 auto;
}

.guidelines-component__code {
    display: none;
    border-top: 1.5em solid lightgray;
}

[id^='code-view']:checked ~ .guidelines-component__code {
    display: block;
}

.guidelines-component__code pre {
    background-color: #1a1a1a;
    color: white;
    padding: 1.25em 1em;
    margin: 0;
    line-height: 2;
    white-space: pre-wrap;
}

.guidelines-component__controls {
    background-color: lightgray;
    border-top: 1.5em solid lightgray;
    text-align: center;
}

.guidelines-component__controls li {
    display: inline-block;
}

.guidelines-component__controls label {
    color: white;
    display: block;
    padding-left: 0.75em;
    padding-right: 0.75em;
    position: relative;
    font-weight: 600;
    line-height: 2;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.guidelines-component__controls label, .guidelines-component__controls label:before {
    transition: background-color 0.15s ease;
}

.guidelines-component__controls label:after {
    content: '';
    opacity: 0;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    transition: opacity 0.15s ease;
}

.guidelines-component__controls label[class*='guidelines-component__controls__presentation']:after {
    border-bottom: 5px solid #1a1a1a;
    bottom: 100%;
}

.guidelines-component__controls label[class*='guidelines-component__controls__code']:after {
    border-top: 5px solid #1a1a1a;
    top: 100%;
}

.guidelines-component__controls label[aria-label]:before {
    content: attr(aria-label);
    opacity: 0;
    padding-left: 0.75em;
    padding-right: 0.75em;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    transition: opacity 0.15s ease;
}

.guidelines-component__controls label[aria-label]:hover:before, .guidelines-component__controls label[aria-label]:focus:before, .guidelines-component__controls label[aria-label]:active:before {
    opacity: 1;
}

.guidelines-component__controls .guidelines-component__controls__presentation--fluid {
    background-color: firebrick;
}

.guidelines-component__controls .guidelines-component__controls__presentation--desktop {
    background-color: #c13c1b;
}

.guidelines-component__controls .guidelines-component__controls__presentation--tablet {
    background-color: #c96418;
}

.guidelines-component__controls .guidelines-component__controls__presentation--mobile {
    background-color: #d58513;
}

.guidelines-component__controls .guidelines-component__controls__code {
    background-color: royalblue;
}

.guidelines-component__controls [class*='guidelines-component__controls__']:before {
    background-color: inherit;
}
