////
/// Guidelines
/// @group guidelines
////

/// @type Length
$guidelines-padding: 1.25rem;
/// @type Length
$guidelines-aside-width: 20rem;

#guidelines h2 {
    margin: 0 0 $guidelines-padding;
}

/**
 * Guidelines Header
 */
.guidelines__header {
    background-image: url('//s1.city.ac.uk/cityr/i/guidelines/background.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    color: white;
    width: 100%;
    padding-bottom: ($guidelines-padding * 3);
    overflow: hidden;

    .guidelines__header__logo {
        background-color: rgba(white, 0.9);
        display: inline-block;
        padding: $guidelines-padding $guidelines-padding
            ($guidelines-padding * 2);
        border-bottom-width: 0;

        span {
            display: none;
        }
    }

    .guidelines__header__title,
    .guidelines__header__description {
        display: block;
        text-align: center;
    }

    .guidelines__header__title {
        margin: ($guidelines-padding * 5) 0 $guidelines-padding;
        font-size: 4rem;
        line-height: 1;
    }

    .guidelines__header__description {
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        font-size: 1.5rem;
        font-weight: 400;
        line-height: 1.5;
    }
}

.guidelines__nav-primary {
    background-color: lightgray;
}
.guidelines__nav-primary__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: ($guidelines-padding * 2) 0 $guidelines-padding !important;
    margin-top: 0;
    margin-bottom: 0;
    list-style: none;

    li {
        display: inline-block;
        transition: width 0.15s;

        @media (max-width: 500px) {
            width: 100%;
        }

        @media (min-width: 501px) and (max-width: 800px) {
            width: 50%;
        }

        @media (min-width: 801px) {
            width: 33.333%;
        }
    }

    a {
        width: 100%;
        display: block;
        padding: 1em;
        border-bottom-width: 0;
        text-decoration: none;
        transition: all 0.3s ease;

        &,
        .guidelines__nav-primary__list__description {
            color: black;
        }

        .guidelines__nav-primary__list__title {
            display: block;
            margin-top: 0;
            margin-bottom: 0.5em;
            font-size: 1.125em;
            font-weight: 600;
            line-height: 1;

            &:after {
                content: '\f105';
                float: right;
                font-size: 1.5em;
                font-family: FontAwesome;
                font-weight: 400;
                line-height: 0.667;
            }
        }

        .gudielines__nav-primary__list__description {
            line-height: 1.5;
        }
    }
}

#guidelines p {
    margin-top: 0;
}

.guidelines__main-content {
    margin-top: ($guidelines-padding * 3);

    &:not(.main-content--full) {
        @media (min-width: 801px) {
            padding-right: ($guidelines-aside-width + $guidelines-padding * 2);
        }
    }
}

.guidelines__aside {
    width: $guidelines-aside-width;

    @media (max-width: 800px) {
        width: 100%;
        margin-bottom: $guidelines-padding;
    }

    @media (min-width: 801px) {
        float: right;
        margin-right: -($guidelines-aside-width + $guidelines-padding * 2);
        border-top: 0.25em solid lightgray;
        padding-top: ($guidelines-padding * 2);
    }

    p:last-child {
        margin-bottom: 0;
    }
}

.guidelines__atomic-icons-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0;
    margin: 0;
    list-style: none;

    li {
        display: inline-block;

        @media (max-width: 500px) {
            width: 50%;
            margin-bottom: ($guidelines-padding / 2);
        }

        @media (min-width: 501px) and (max-width: 800px) {
            width: 25%;
        }

        @media (min-width: 801px) {
            width: 50%;
            margin-bottom: ($guidelines-padding / 2);
        }
    }

    a {
        box-sizing: content-box;
        background-size: 90% auto;
        background-position: center top;
        background-repeat: no-repeat;
        color: gray;
        display: block;
        padding-top: 90%;
        border-bottom-width: 0;
        height: 1.5em;
        font-weight: bold;
        line-height: 1.5;
        text-align: center;

        &.guidelines__atomic-icons-list__atoms {
            background-image: url(data:image/svg+xml;base64,);
        }
        &.guidelines__atomic-icons-list__molecules {
            background-image: url(data:image/svg+xml;base64,);
        }
        &.guidelines__atomic-icons-list__organisms {
            background-image: url(data:image/svg+xml;base64,);
        }
        &.guidelines__atomic-icons-list__templates {
            background-image: url(data:image/svg+xml;base64,);
        }
    }
}

.guidelines__case-studies {
    margin-top: ($guidelines-padding * 2);
    border-top: 2px solid #b91b2b;
    padding-top: ($guidelines-padding * 2);

    h3 {
        margin: 0 0 ($guidelines-padding * 2);
        text-align: center;
    }
}

.guidelines__case-studies__list {
    padding: 0;
    margin: 0 ($guidelines-padding * -0.5);
    list-style: none;
    font-size: 0;

    li {
        display: inline-block;
        padding: 0 ($guidelines-padding / 2) $guidelines-padding;
        font-size: $font-size-base;

        @media (max-width: 500px) {
            width: 100%;
        }

        @media (min-width: 501px) and (max-width: 800px) {
            width: 50%;
        }

        @media (min-width: 801px) {
            width: 25%;
        }
    }

    .guidelines__case-studies__list__media {
        position: relative;

        img {
            width: 100%;
            display: block;
        }

        .icon {
            background-color: red;
            color: white;
            width: 3em;
            height: 3em;
            border-bottom-width: 0;
            position: absolute;
            bottom: 0;
            left: 0;
            text-align: center;
            text-decoration: none;
            line-height: 3;
        }
    }

    .guidelines__case-studies__list__title,
    .guidelines__case-studies__list__description {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .guidelines__case-studies__list__title {
        margin: $guidelines-padding 0;
    }

    .guidelines__case-studies__list__description {
        margin: 0;
    }
}

////
/// Helpers
////
.guidelines-components {
    text-align: center;
}

#guidelines [aria-hidden='true'] {
    display: none;
}

////
/// Toggling
////
$toggles: fluid 100vw, desktop 1200px, tablet 768px, mobile 500px;
@each $toggle in $toggles {
    $toggle-name: nth($toggle, 1);
    $toggle-width: nth($toggle, 2);

    [id^='presentation-view--#{$toggle-name}']:checked {
        & ~ .guidelines-component__presentation {
            @if $toggle-name == 'fluid' {
                width: 100vw;
            }

            iframe,
            .guidelines-component__presentation__contents {
                width: #{$toggle-width};

                @if $toggle-name == 'fluid' {
                    max-width: 100%;
                }
            }
        }

        &
            ~ .guidelines-component__controls
            .guidelines-component__controls__presentation--#{$toggle-name} {
            background-color: mix(white, black, 10%) !important;

            &:after {
                opacity: 1;
            }
        }
    }
}
[id^='code-view']:checked {
    & ~ .guidelines-component__code {
        display: block;
    }

    & ~ .guidelines-component__controls .guidelines-component__controls__code {
        background-color: mix(white, black, 10%) !important;

        &:after {
            opacity: 1;
        }
    }
}

////
/// Component Base
////
.guidelines-component {
    background-color: lightgray;
    width: auto;
    max-width: 100%;
    display: inline-block;
    padding: 1.5em 1em;
    margin: 0 auto;
    position: relative;
    text-align: left;

    & + & {
        margin-top: 1.5em;
    }
}

////
/// Component Title
////
.guidelines-component__title {
    padding-bottom: 1em;
}

////
/// Component Pieces
////
.guidelines-component__presentation,
.guidelines-component__presentation iframe,
.guidelines-component__presentation__contents,
.guidelines-component__controls,
.guidelines-component__code {
    transition: width 0.5s ease;
}

////
/// Component Presentation
////
.guidelines-component__presentation {
    background-color: white;
    max-width: 100%;
    border: 2px solid #b21c1d;
    position: relative;
    overflow-x: auto;
    overflow-y: hidden;

    iframe,
    .guidelines-component__presentation__contents {
        display: block;
        border: 0;
    }
}

////
/// Component Controls and Code
////
.guidelines-component__controls,
.guidelines-component__code {
    max-width: 768px;
    display: block;
    margin: 0 auto;
}

////
/// Component Code
////
.guidelines-component__code {
    display: none;
    border-top: 1.5em solid lightgray;

    [id^='code-view']:checked ~ & {
        display: block;
    }

    pre {
        background-color: mix(white, black, 10%);
        color: white;
        padding: 1.25em 1em;
        margin: 0;
        line-height: 2;
        white-space: pre-wrap;
    }
}

////
/// Component Controls
////
.guidelines-component__controls {
    background-color: lightgray;
    border-top: 1.5em solid lightgray;
    text-align: center;

    li {
        display: inline-block;
    }

    label {
        color: white;
        display: block;
        padding-left: 0.75em;
        padding-right: 0.75em;
        position: relative;
        font-weight: 600;
        line-height: 2;
        cursor: pointer;
        user-select: none;

        &,
        &:before {
            transition: background-color 0.15s ease;
        }

        &:after {
            content: '';
            opacity: 0;
            border-left: 5px solid transparent;
            border-right: 5px solid transparent;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            transition: opacity 0.15s ease;
        }
        &[class*='guidelines-component__controls__presentation']:after {
            border-bottom: 5px solid mix(white, black, 10%);
            bottom: 100%;
        }
        &[class*='guidelines-component__controls__code']:after {
            border-top: 5px solid mix(white, black, 10%);
            top: 100%;
        }

        &[aria-label] {
            &:before {
                content: attr(aria-label);
                opacity: 0;
                padding-left: 0.75em;
                padding-right: 0.75em;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                text-align: center;
                transition: opacity 0.15s ease;
            }

            &:hover,
            &:focus,
            &:active {
                &:before {
                    opacity: 1;
                }
            }
        }
    }

    .guidelines-component__controls__presentation--fluid {
        background-color: firebrick;
    }
    .guidelines-component__controls__presentation--desktop {
        background-color: mix(orange, firebrick, 20%);
    }
    .guidelines-component__controls__presentation--tablet {
        background-color: mix(yellow, firebrick, 30%);
    }
    .guidelines-component__controls__presentation--mobile {
        background-color: mix(yellow, firebrick, 45%);
    }
    .guidelines-component__controls__code {
        background-color: royalblue;
    }
    [class*='guidelines-component__controls__'] {
        &:before {
            background-color: inherit;
        }
    }
}

$guidelines-component-views: () !default;
